import Vue from 'vue'
import VueRouter from 'vue-router'
// import params from '../components/goods/params.vue'
const params = () => import(/* webpackChunkName:"goods" */ '../components/goods/params.vue')
// import categories from '../components/goods/categories.vue'
const categories = () => import(/* webpackChunkName:"categories" */ '../components/goods/categories.vue')
// 订单模块
// import orders from '../components/orders/orders.vue'
const orders = () => import(/* webpackChunkName:"categories" */ '../components/orders/orders.vue')
// 数据模块
// import reports from '../components/reports/reports.vue'
const reports = () => import(/* webpackChunkName:"categories" */ '../components/reports/reports.vue')

// // 导入home的二级组件welcome
// import Welcome from '@/components/Welcome'
// 导入home的二级组件User
// import User from '@/components/users/User'
const User = () => import(/* webpackChunkName:"power" */ '@/components/users/User')

// 导入home的二级组件Roles
// import Roles from '@/components/power/Roles'
const Roles = () => import(/* webpackChunkName:"power" */ '@/components/power/Roles')

// import Rights from '@/components/power/Rights'
const Rights = () => import(/* webpackChunkName:"power" */ '@/components/power/Rights')

// 商品模块
// import goods from '../components/goods/goods.vue'
const goods = () => import(/* webpackChunkName:"goods" */ '../components/goods/goods.vue')

// import addGoods from '../components/goods/addGoods.vue'
const addGoods = () => import(/* webpackChunkName:"goods" */ '../components/goods/addGoods.vue')
// 导入登录组件
// import Login from '@/components/Login'
const Login = () => import(/* webpackChunkName:"login_home" */ '@/components/Login')
// 导入首页组件
// import Home from '@/components/Home'
const Home = () => import(/* webpackChunkName:"login_home" */ '@/components/Home')
Vue.use(VueRouter)

const routes = [
  { path: '/', redirect: '/login' },
  { path: '/login', component: Login },
  {
    path: '/home',
    component: Home,
    children: [
      { path: '/home', component: User },
      { path: '/users', component: User },
      { path: '/roles', component: Roles },
      { path: '/rights', component: Rights },
      // 商品模块
      { path: '/goods', component: goods },
      { path: '/goods/add', component: addGoods },
      { path: '/params', component: params },
      { path: '/categories', component: categories },
      // 订单模块
      { path: '/orders', component: orders },
      // 数据报表模块
      { path: '/reports', component: reports }
    ]
  }
]

const router = new VueRouter({
  routes
})

// 设置路由拦截
router.beforeEach((to, from, next) => {
  // 获取本地存储的token
  const token = window.localStorage.getItem('token')

  // 路由值变化之前拦截
  // 如果去的新页面是登录页面就直接放行，不做校验token
  if (to.path === '/login') {
    next()
  } else {
    // 判断是否有token
    if (!token) {
      next('/login')
    } else {
      next()
    }
  }
})

export default router
