import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
// 导入全局样式文件
import '@/assets/css/common.css'
// 导入字体图标
import '@/assets/fonts/iconfont.css'
// 导入ele-ui
// import ElementUI from 'element-ui'
// import 'element-ui/lib/theme-chalk/index.css'
// 导入全局的面包屑导航
import bread from '@/components/com_Components/bread.vue'
// 导入树状图标
// import TreeTable from 'vue-table-with-tree-grid'
// 导入日期格式化组件
import dayjs from 'dayjs'
// 导入
import NProgress from 'nprogress'
// import 'nprogress/nprogress.css'
// 富文本
// import 'quill/dist/quill.core.css'
// import 'quill/dist/quill.snow.css'
// import 'quill/dist/quill.bubble.css'

// 导入axios
import axios from 'axios'
// 定义全局的过滤器
Vue.filter('format', time => {
  return dayjs(time).format('YYYY-MM-DD HH:mm:ss ')
})
// 全局注册组件
// Vue.component('tree-table', TreeTable)
Vue.component('bread', bread)
// 注册
// Vue.use(ElementUI)
// 设置请求根路径
// axios.defaults.baseURL = 'http://47.94.247.66:8888/api/private/v1'
axios.defaults.baseURL = 'https://www.liulongbin.top:8888/api/private/v1'
// axios.defaults.baseURL = 'http://192.168.68.23:8080/api/private/v1'
// axios.defaults.baseURL = 'http://192.168.68.101:8888/api/private/v1'
// 添加请求拦截器
axios.interceptors.request.use(
  function (response) {
    // 在发送请求之前做些什么
    // 设置请求拦截
    response.headers.Authorization = localStorage.getItem('token')
    NProgress.start()
    return response
  },
  function (error) {
    // 对响应错误做些什么
    return Promise.reject(error)
  }
)

// 添加响应拦截器
axios.interceptors.response.use(
  function (response) {
    // 在发送请求之前做些什么
    NProgress.done()
    return response
  },
  function (error) {
    // 对响应错误做些什么
    return Promise.reject(error)
  }
)
// 仅导入，组件无法使用，故挂载到原型上共享
Vue.prototype.axios = axios

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
